import { sanity } from "@/lib/sanity";
import { NextResponse } from "next/server";

export async function GET(
  req: Request,
  { params }: { params: { id: string } },
) {
  const cleanId = params.id.split("?")[0];

  const email = await sanity.fetch(`*[_type == "emails" && _id == $id][0]`, {
    id: cleanId,
  });

  if (!email) {
    return NextResponse.json({ error: "Email not found" }, { status: 404 });
  }

  return NextResponse.json(email);
}

export async function PUT(
  req: Request,
  { params }: { params: { id: string } },
) {
  const body = await req.json();
  const cleanId = params.id.split("?")[0];

  await sanity.patch(cleanId).set(body).commit();

  const updatedEmail = await sanity.fetch(
    `*[_type == "emails" && _id == $id][0]`,
    { id: cleanId },
  );
  return NextResponse.json({ success: true, email: updatedEmail });
}
